import React from 'react';

const Card = ({ name, email, id, lname }) => {
    return (
        <div className='tc grow pa3 bg-gradient ma5 mt3 dib'>
            <img alt='robots' src={`https://reqres.in/img/faces/${id}-image.jpg`} />
            <div>
                <h2>{name} {lname}</h2>
                <h3>{email}</h3>
            </div>
        </div>
    );
}

export default Card;
