import Loader from "react-loader-spinner";
import './styles.css';

const LoaderComp = ()=>{
    return(
        //used to display the loader when the api is fetching data
        <div className='app'>
            <Loader
            type="Puff"
            color="aquamarine"
            // style={{ marginLeft: '270px' }}
            height={150}
            width={150}
            timeout={10000}
            />
       </div>
    );
}
export default LoaderComp
